.P-Login {
    height: 100%;
    background: #f0f2f5;
    .main-agileits {
        padding-top: 100px;
        width: 30%;
        margin: 0 auto;
    }
    .form-w3-agile h2 {
        color: #fff;
        font-size: 20px;
        font-weight: 500;
        text-align: center;
        margin-bottom: 2em;
        letter-spacing: 2px;
    }
    .form-w3-agile {
        background: rgba(16, 142, 233, 0.6);
        -webkit-box-shadow: 0px 35px 44px -22px rgba(0,0,0,0.72);
        -moz-box-shadow: 0px 35px 44px -22px rgba(0,0,0,0.72);
        box-shadow: 0px 35px 44px -22px #1f181b;
        padding: 60px 40px;
    }
    h1 {
        text-align: center;
        font-size: 36px;
        font-weight: bold;
        padding: 50px 0;
        color: #333333;
        letter-spacing: 4px;
    }
    .icon-w3 i {
        color: #ffffff;
        font-size: 16px;
    }
    .icon-w3 {
        display: inline-block;
        position: absolute;
        right: 12px;
        bottom: 43px;
    }
    form {
        margin: 0 auto;
        width: 100%;
    }
    .form-sub-w3{
        position:relative;
    }
    .form-sub-w3 input[type="text"],input[type="password"] {
        outline: none;
        font-size: 15px;
        color: #ffffff;
        padding: 15px 40px 15px 10px;
        width: 87%;
        border: none;
        border-bottom: 1px solid #DCCBCF;
        appearance: none;
        -webkit-appearance: none;
        margin-bottom: 30px;
        background: transparent;
        
    }
    .form-sub-w3 input[type="password"] {
        outline: none;
        font-size: 15px;
        color: #ffffff;
        padding: 15px 40px 15px 10px;
        width: 87%;
        border: none;
        border-bottom: 1px solid #DCCBCF;
        appearance: none;
        -webkit-appearance: none;
        margin-bottom: 30px;
        background: transparent;
        
    }
    input[type="button"] {
        background: #fff;
        color: #000;
        outline: none;
        display: block;
        margin: 0 auto;
        border: none;
        cursor: pointer;
        padding: 9px 38px;
        font-size: 14px;
        margin-top: 55px;
        font-weight: bold;
        text-transform: uppercase;
        
        transition: 0.3s all;
        -webkit-transition: 0.3s all;
        -moz-transition: 0.3s all;
        -o-transition: 0.3s all;
        -ms-transition: 0.3s all;
    }
    input[type="button"]:hover{
        background: #001529;
        color:#ffffff;
        transition: 0.3s all;
        -webkit-transition: 0.3s all;
        -moz-transition: 0.3s all;
        -o-transition: 0.3s all;
        -ms-transition: 0.3s all;
    }
    .p-bottom-w3ls {
        font-size: 13px;
        color: #eee;
        margin-top: 1em;
        text-align: center;
        text-transform: none;
    }
    .p-bottom-w3ls a{
        color: #001529;
        outline:none;
        text-decoration:none;
    }
    /*--main-content-ends-here--*/
    /*--register-form--*/
    .contact-w3-agileits h3 {
        color: #fff;
        font-size: 20px;
        font-weight: 500;
        text-align: center;
        margin-bottom: 2em;
        letter-spacing: 2px;
    }
    .contact-form1 {
        padding: 60px 40px; 
        background: #444;
    }
    #small-dialog, #small-dialog1, #small-dialog2 {
        max-width: 541px;
    }
    .my-mfp-zoom-in.mfp-ready.mfp-bg {
        opacity: .9;
    }
    .contact-w3-agileits input[type="text"],input[type="email"],input[type="password"]{
         outline: none;
        font-size: 15px;
        color: #ffffff;
        padding: 15px 40px 15px 10px;
        width: 87%;
        border: none;
        border-bottom: 1px solid #0b0b0b;
        appearance: none;
        -webkit-appearance: none;
        margin-bottom: 20px;
        background: transparent;
        
    }
    .form-sub-w3ls{
        position:relative;
    }
    .icon-agile{
        display: inline-block;
        position: absolute;
        right: 12px;
        bottom: 35px;
    }
    .icon-agile i{
        color: #ffffff;
        font-size: 16px;
    }
    label.checkbox {
        display: block;
        margin: 15px 0px 0px 3px;
        text-align: left;
    }
    label p {
            color: #000;
        font-size: 12px;
        font-weight: bold;
        margin-left: 8px;
        display: inline-block;
    }
    
    /*--//register-form--*/
    /*--copy-right-starts-here--*/
    .copyright p {
